import React, { useState, useEffect } from "react";
import { useNavigate } from "react-router-dom";
import { GlobalStyle, Container, SubContainer } from "../../Styles/reset.style";
import {
  PetInfo,
  StyledPetInfo,
  ImageWrap,
  ImageUpBtn,
  ProfileImage,
  SelectInfo,
  SelectInfoItem,
  Overlay,

} from "../../Components/Join/JoinPage.style";
import {
  TitleWrap,
  SubmitButton,
  InputField,
  StyledInput,
  FieldLabel,
  Label,
} from "../../Components/Login/LoginForm.style";
import PopupModal from '../../Components/Common/Modal/PopupModal'
import * as DropdownComponents from "../../Components/Profile/Dropdown";
import HeaderLayouts from "../../Components/Common/Header/Header";

const JoinPage = () => {
  const navigate = useNavigate();
  const [username, setUsername] = useState("");
  const [email, setEmail] = useState("");
  const [emailError, setEmailError] = useState("");
  const [accountnameError, setAccountnameError] = useState("");
  const [emailAvailable, setEmailAvailable] = useState(false); //  
  const [password, setPassword] = useState("");
  const [accountname, setAccountname] = useState("");
  const [intro, setIntro] = useState("");
  const [imgSrc, setImgSrc] = useState(
    "https://api.mandarin.weniv.co.kr/1698644803298.jpg"
  );
  const [validationErrors, setValidationErrors] = useState({});
  const [info, setInfo] = useState("");
  const [currentPage, setCurrentPage] = useState("join");
  const [showModal, setShowModal] = useState(false)
  const [isModalOpen, setIsModalOpen] = useState(false)
  const [isModalVisible, setIsModalVisible] = useState(false);
  const [isAccountNameValid, setIsAccountNameValid] = useState(false);
  const [pet, setPet] = useState("");
  const [gender, setGender] = useState("");
  const [birthdate, setBirthdate] = useState("");
  const [location, setLocation] = useState("");
  const [usernameTouched, setUsernameTouched] = useState(false);
  const [previewImage, setPreviewImage] = useState(null);
  const [usernameError, setUsernameError] = useState('');
  const convertInfoToTags = (bangyeolgori) => {
    return `#bangyeolgori `;
  };
  
  //  
  const handleUsernameBlur = () => {
    setUsernameTouched(true);
  };
  
  const handleStartBanGyeol = () => {
    setIsModalOpen(true);
  }

  const handleLogin = () => {
    navigate('/login');
  }

  const validateForm = () => {
    const errors = {};
    const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
    const accountNameRegex = /^[a-zA-Z0-9_.]+$/;

    if (!email || !emailRegex.test(email))
      errors.email = "  .";
    if (!password || password.length < 6)
      errors.password = " 6  .";
    if (!accountname || !accountNameRegex.test(accountname))
      errors.accountname = ", , ,    .";

    setValidationErrors(errors);
    return Object.keys(errors).length === 0;
  };

  const validateEmail = (email) => {
    const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
    if (!emailRegex.test(email)) {
      setEmailError("  .");
    } else {
      setEmailError("");
    }
  };

  const goToProfilePage = () => {
    setCurrentPage("profile");
  };

  const validateAccountname = (accountname) => {
    const accountNameRegex = /^[a-zA-Z0-9_.]+$/;
    if (!accountNameRegex.test(accountname)) {
      setAccountnameError(", , ,    .");
      setIsAccountNameValid(false); // Set account name as invalid
    } else {
      setAccountnameError("");
      setIsAccountNameValid(true); // Set account name as valid
    }
  };

  //  
  const checkEmailAvailability = async (email) => {
    setEmailError(""); //    

    if (!email) {
      return;
    }

    const reqUrl = "https://api.mandarin.weniv.co.kr/user/emailvalid";
    const res = await fetch(reqUrl, {
      method: "POST",
      headers: {
        "Content-type": "application/json",
      },
      body: JSON.stringify({ user: { email } }),
    });

    const data = await res.json();

    if (res.ok) {
      setEmailError(data.message); // API     
    } else {
      setEmailError("    .");
    }
  };

  //       
  const handlePasswordFocus = async () => {
    await checkEmailAvailability(email);
  };

  const join = async (joinData) => {
    const reqUrl = "https://api.mandarin.weniv.co.kr/user/";
    const res = await fetch(reqUrl, {
      method: "POST",
      headers: {
        "Content-type": "application/json",
      },
      body: JSON.stringify(joinData),
    });
    const json = await res.json();

    if (res.status === 422) {
      alert(json.message);
    } else {
      console.log(json);
    }

    return res;
  };

  // 
  const inputUsername = (e) => {
    const newUsername = e.target.value;
    setUsername(newUsername);
  
    if (newUsername.length > 0 && newUsername.length < 2) {
      setUsernameError('2  .');
    } else if (newUsername.length > 10) {
      setUsernameError('10  .');
    } else {
      setUsernameError('');
    }
  };

  const extractIntro = () => {
    const match = info.match(/#intro:([^#]*)/);
    return match ? match[1].trim() : "";
  };

  const uploadImage = async (imageFile) => {
    const baseUrl = "https://api.mandarin.weniv.co.kr/";
    const reqUrl = baseUrl + "image/uploadfile";
    const form = new FormData();
    form.append("image", imageFile);
    const res = await fetch(reqUrl, {
      method: "POST",
      body: form,
    });
    const json = await res.json();
    const imageUrl = baseUrl + json.filename;
    setImgSrc(imageUrl);
  };
  const handleChangeImage = (e) => {
    const imageFile = e.target.files[0];
    uploadImage(imageFile);
  };

  //   
  const checkAccountNameAvailability = async (accountname) => {
    const accountNameRegex = /^[a-zA-Z0-9_.]+$/;
    if (!accountNameRegex.test(accountname)) {
      setAccountnameError(", , ,    .");
      return;
    }

    const reqUrl = "https://api.mandarin.weniv.co.kr/user/accountnamevalid";
    const res = await fetch(reqUrl, {
      method: "POST",
      headers: {
        "Content-type": "application/json",
      },
      body: JSON.stringify({ user: { accountname } }),
    });

    const data = await res.json();

    if (res.ok) {
      setAccountnameError(data.message); // API     
    } else {
      setAccountnameError("     .");
    }
  };

  //        
  const handleUsernameFocus = async () => {
    await checkAccountNameAvailability(accountname);
  };

  const handleIntroChange = () => {
    const hashtags = [
      `#intro:${intro}`,
      `#pet:${pet}`,
      `#gender:${gender}`,
      `#birthdate:${birthdate}`,
      `#location:${location}`,
      convertInfoToTags("bangyeolgori"),
    ];
    setInfo(hashtags.join(" "));
  };

  const handleSubmit = async () => {
    const reqUrl = "https://api.mandarin.weniv.co.kr/user/";
    const res = await fetch(reqUrl, {
      method: "POST",
      headers: {
        "Content-type": "application/json",
      },
      body: JSON.stringify({
        email: email,
        password: password,
      }),
    });

    if (res.ok) {
      setCurrentPage("profile");
    } else {
      const data = await res.json();
      alert(data.message);
    }
  };

  const submitJoin = () => {
    console.log("Starting the join process"); //   

    if (!validateForm()) {
      console.log("Validation Error: Form is not valid."); //   
      return;
    }

    console.log("Form is valid. Proceeding to join."); //   ,  

    if (!/^[a-zA-Z0-9_.]+$/.test(accountname)) {
      console.log("Validation Error: Account name is not valid."); //     
      setAccountnameError(", , ,    .");
      return;
    }
  
    console.log("Form is valid. Proceeding to join.");

    const joinData = {
      user: {
        username: username,
        email: email,
        password: password,
        accountname: accountname,
        intro: info,
        image: imgSrc,
      },
    };

    join(joinData).then((response) => {
      if (response && response.status !== 422) {
        console.log("Join successful."); //  
        setShowModal(true);
      } else {
        console.log("Join Error: An error occurred during the join process."); //    
      }
    });
  };

  const isValidEmailAndPassword = () => {
    return email.includes("@") && password.length >= 8;
  };

  const isValidProfile = () => {
    return username.length >= 2 && accountname.length >= 2;
  };

  return (
    <>
      <GlobalStyle />
      <Container>
        <HeaderLayouts back={true} />
        {/* <FormWrapper> */}

          {currentPage === "join" && (
            <>
            <TitleWrap> </TitleWrap>
              <SubContainer>
                <InputField>
                  <FieldLabel></FieldLabel>
                  <StyledInput
                    type="email"
                    placeholder=" ."
                    value={email}
                    onChange={(e) => setEmail(e.target.value)}
                  />
                  {emailError && (
                    <span style={{ fontSize:"14px", color: "red" }}>{emailError}</span>
                  )}
                </InputField>
                <InputField>
                <FieldLabel></FieldLabel>
                <StyledInput
                  type="password"
                  placeholder=" ."
                  value={password}
                  onChange={(e) => {
                    setPassword(e.target.value);
                    // Call validateForm function here if you want to validate as the user types
                  }}
                  onFocus={handlePasswordFocus} //       
                />
                {password.length > 0 && password.length < 6 && (
                  <span style={{ fontSize: "14px", color: "red" }}> 6 .</span>
                )}
              </InputField>
  
                <SubmitButton
                  type="button"
                  onClick={handleSubmit}
                  disabled={!isValidEmailAndPassword()}
                >
                  
                </SubmitButton>
              </SubContainer>
            </>
          )}

          {currentPage === "profile" && (
            <>
            <TitleWrap> </TitleWrap>
              {/*   */}
              <SubContainer>
                <ImageWrap>
                    {previewImage ? (
                    <ProfileImage src={previewImage} alt="Profile Preview" />
                    ) : (
                    <ProfileImage src={imgSrc} alt="Profile" />
                    )}
                    <ImageUpBtn uploaded={!!previewImage}>
                    <input type="file" onChange={handleChangeImage} />
                    </ImageUpBtn>
                </ImageWrap>
                <InputField>
                <FieldLabel></FieldLabel>
                <StyledInput
                  type="text"
                  placeholder="2 ~ 10  ."
                  minLength="2"
                  maxLength="10"
                  value={username}
                  onChange={inputUsername}
                />
                {usernameError && (
                  <span style={{ fontSize: "14px", color: "red" }}>{usernameError}</span>
                )}
              </InputField>
                <InputField>
                  <FieldLabel> ID</FieldLabel>
                  <StyledInput
                    type="text"
                    placeholder=", , (.),(_)  ."
                    onChange={(e) => {
                      setAccountname(e.target.value);
                      validateAccountname(e.target.value);
                    }}
                    onBlur={handleUsernameFocus} //         
                  />
                  {accountnameError && (
                    <span style={{ fontSize:"14px", color: "red" }}>{accountnameError}</span>
                  )}
                </InputField>
                <InputField>
                  <Label></Label>
                  <StyledInput
                    type="text"
                    placeholder="    !"
                    value={intro}
                    onChange={(e) => setIntro(e.target.value)}
                  />
                </InputField>
                <PetInfo>
                  <StyledPetInfo> </StyledPetInfo>
                  <SelectInfo>
                    <SelectInfoItem>
                      <label></label>
                      <DropdownComponents.DropdownSelect
                        value={pet}
                        onChange={(e) => {
                          setPet(e.target.value);
                          handleIntroChange();
                        }}
                        options={DropdownComponents.petOptions}
                      />
                    </SelectInfoItem>
    
                    <SelectInfoItem>
                      <label></label>
                      <DropdownComponents.DropdownSelect
                        value={gender}
                        onChange={(e) => {
                          setGender(e.target.value);
                          handleIntroChange();
                        }}
                        options={DropdownComponents.genderOptions}
                      />
                    </SelectInfoItem>
    
                    <SelectInfoItem>
                      <label></label>
                      <input
                        type="date"
                        value={birthdate}
                        onChange={(e) => {
                          setBirthdate(e.target.value);
                          handleIntroChange();
                        }}
                      />
                    </SelectInfoItem>
    
                    <SelectInfoItem>
                      <label></label>
                      <DropdownComponents.DropdownSelect
                        value={location}
                        onChange={(e) => {
                          setLocation(e.target.value);
                          handleIntroChange();
                        }}
                        options={DropdownComponents.locationOptions}
                      />
                    </SelectInfoItem>
                  </SelectInfo>
                </PetInfo>
                <SubmitButton
                type="button"
                onClick={() => {
                  if (isValidProfile() && isAccountNameValid) {
                    submitJoin();
                    handleStartBanGyeol();
                  }
                }}
                disabled={!isValidProfile() || !isAccountNameValid}
              >
                 
              </SubmitButton>
              </SubContainer>
            </>
          )}

        {/*  */}
        {isModalOpen &&(
          <>
            <Overlay onClick={()=> setIsModalOpen(false)}/>
            <PopupModal 
              isVisible={isModalOpen}
              setIsVisible={setIsModalOpen}
              alertText="   "
              confirmText=""
              onConfirm={handleLogin}
            />
          </>
        )}
      </Container>
    </>
  );
};

export default JoinPage;
