export function DropdownSelect({ value, onChange, options }) {
    return (
        <select value={value} onChange={onChange}>
            {/* ""  */}
            {options.map((option, index) => (
                <option value={option.value} key={index}>
                    {option.label}
                </option>
            ))}
        </select>
    );
}
