import React, { useEffect, useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { isLoggedIn } from '../../authService';
import { LogoIcon, LogoText, SplashContainer, SplashContent, SubText } from '../../Components/Splash/Splash.styles';
import logoPetlink from '../../assets/image/logo-petlink.png';

function SplashPage() {
  const navigate = useNavigate();
  const [isVisible, setIsVisible] = useState(true); // isVisible   

  useEffect(() => {
    const timer = setTimeout(() => {
      setIsVisible(false); //  false    .
      console.log('Checking login status...');
      if (isLoggedIn()) {
        console.log('User is logged in, redirecting to /home');
        navigate('/home');
      } else {
        console.log('User is not logged in, redirecting to /login');
        navigate('/login')
      }
    }, 3000); // 3 

    return () => {
      clearTimeout(timer);
    };
  }, [navigate]);

  return (
    isVisible && (
      <SplashContainer>
        <SplashContent>
          <LogoIcon src={logoPetlink} alt=" " />
          <LogoText>
            {"".split("").map((char, index) => (
              <span key={index}>{char}</span>
            ))}
          </LogoText>
          <SubText>  </SubText>
        </SplashContent>
      </SplashContainer>
    )
  );
}

export default SplashPage;