import React, { useState } from 'react';
import { ModalOverlay, ModalContainer, ModalAlertText, ModalButtonContainer, ModalButton } from './Modal.style';

function PopupModal({ 
    isVisible, //     (true or false)
    setIsVisible, //     
    alertText,  //   
    cancelText,  //   
    confirmText,  //   
    onConfirm,  //      
    onCancel,  //      
    onDelete //   prop onDelete 
}) {
  const handleConfirm = () => {
    if(onConfirm) onConfirm();
    if(onDelete) onDelete(); //  onDelete 
    setIsVisible(false);
  };

  const handleCancel = () => {
    if(onCancel) onCancel();
    setIsVisible(false);
  };

  return isVisible ? (
    <ModalOverlay>
      <ModalContainer>
        <ModalAlertText>{alertText}</ModalAlertText>
        <ModalButtonContainer>
          {cancelText && (
            <ModalButton onClick={handleCancel}>{cancelText}</ModalButton>
          )}
          {confirmText && (
            <ModalButton $isConfirm onClick={handleConfirm}>{confirmText}</ModalButton>
          )}
        </ModalButtonContainer>
      </ModalContainer>
    </ModalOverlay>
  ) : null;
}

export default PopupModal;
