import React, { useState, useEffect } from 'react';
import { GlobalStyle, Container, SubContainer } from '../../Styles/reset.style';
import { Header, HeaderButton, Required, SaveButton, AddImg, InputTitle, InputImg, AddImgBtn, PostInfo, CategoryContainer, DropdownSelect, AddTxtForm } from './CommunityUpload.style'
import { useNavigate } from 'react-router-dom';
import backBtn from '../../assets/image/icon-arrow-left.png';
import imgBtn from '../../assets/image/icon-img-button.png';
import PopupModal from '../../Components/Common/Modal/PopupModal';
import axios from 'axios';

function CustomInput({ title, isRequired, placeholder, type = "text", value, onChange }) {
  if (type === "dropdown") {
    return (
      <PostInfo>
        <InputTitle>
          {title}
          {isRequired && <Required>*</Required>}
        </InputTitle>
        <select value={value} onChange={onChange}>
          <option value=""></option>
          <option value=" "> </option>
          <option value=" "> </option>
          <option value=" "> </option>
          <option value=" "> </option>
        </select>
      </PostInfo>
    );
  }

  if (type === "textarea") {
    return (
      <PostInfo>
        <InputTitle>
          {title} 
          {isRequired && <Required>*</Required>}
        </InputTitle>
        <textarea placeholder={placeholder} value={value} onChange={onChange} rows="10" />
      </PostInfo>
    );
  }

  return (
    <PostInfo>
        <InputTitle>
          {title} 
          {isRequired && <Required>*</Required>}
        </InputTitle>
      <input type={type} placeholder={placeholder} value={value} onChange={onChange} maxLength={title ? 15 : undefined} />
    </PostInfo>
  );
}

export default function CommunityUploadPage() {
    const navigate = useNavigate();

    const [title, setTitle] = useState('');
    const [content, setContent] = useState('');
    const [category, setCategory] = useState('');
    const [imgUrl, setImgUrl] = useState('');
    const [showModal, setShowModal] = useState(false);
    const [isAllFieldsFilled, setIsAllFieldsFilled] = useState(false);

    useEffect(() => {
      const isTitleValid = title.length >= 2 && title.length <= 15;
      setIsAllFieldsFilled(category && isTitleValid && content.trim() !== '');
    }, [title, category, content]); // title, category, content   .

    const handleImageUpload = async (e) => {
      const file = e.target.files[0];
      const formData = new FormData();
      formData.append('image', file);
    
      try {
        const res = await axios.post('https://api.mandarin.weniv.co.kr/image/uploadfile', formData, {
          headers: {
            'Content-Type': 'multipart/form-data',
          },
        });
        
        let filename = res.data.filename;
        const baseUrl = 'https://api.mandarin.weniv.co.kr/';
        const fullPath = baseUrl + filename;
        setImgUrl(fullPath);
      } catch (err) {
        console.error(err);
      }
    };
    
    const handlePostSubmit = async (e) => {
        e.preventDefault();

      //    (2~15 )
        const isTitleValid = title.length >= 2 && title.length <= 15;

        //     
        const isAllFieldsFilled = category && isTitleValid && content.trim();

        //    
        if (!category) {
          alert(" .");
          return;
        }

        //    
        if (!isTitleValid) {
          alert(" 2~15  .");
          return;
        }

        //    
        if (!content.trim()) {
          alert(" .");
          return;
        }

        //      
        if (!isAllFieldsFilled) {
          alert("  .");
          return;
        }
  
      const token = localStorage.getItem('token');
      const postData = {
        post: {
        content: JSON.stringify({
            title: title,
            category: category,
            contentText: content
        }),
        image: imgUrl
    }
      };
  
      try {
        const res = await axios.post('https://api.mandarin.weniv.co.kr/post', postData, {
          headers: {
            Authorization: `Bearer ${token}`,
            'Content-Type': 'application/json',
          },
        });
        
        console.log(res.data);
        navigate('/community'); 

      } catch (err) {
        console.error(err);
      }
    };

    return (
        <>
            <GlobalStyle />
            <Container>
            <Header>
              <HeaderButton onClick={() => setShowModal(true)}><img src={backBtn} alt=" " /></HeaderButton>
              {/* isAllFieldsFilled  SaveButton isActive prop  */}
              <SaveButton isActive={isAllFieldsFilled} onClick={handlePostSubmit}></SaveButton>
            </Header>
            <PopupModal 
                isVisible={showModal}
                setIsVisible={setShowModal}
                onConfirm={() => navigate('/community')}
                onCancel={() => console.log('Cancel')}
                alertText=" ?"
                cancelText=""
                confirmText=""
            />
            <SubContainer>
                <CategoryContainer>
                    <InputTitle> <Required>*</Required></InputTitle>
                    <DropdownSelect value={category} onChange={e => setCategory(e.target.value)}>
                        <option value=""></option>
                        <option value=" "> </option>
                        <option value=" "> </option>
                        <option value=" "> </option>
                        <option value=" "> </option>
                    </DropdownSelect>
                </CategoryContainer>
                <AddTxtForm>
                <CustomInput
                    title=""
                    isRequired={true}
                    placeholder="2~15  ."
                    value={title}
                    onChange={e => setTitle(e.target.value)}
                />
                <CustomInput
                    title=""
                    isRequired={true}
                    type="textarea"
                    placeholder="  ."
                    value={content}
                    onChange={e => setContent(e.target.value)}
                />
                </AddTxtForm>
                <AddImg>
                    <InputTitle> </InputTitle>
                    <InputImg img={imgUrl}>
                        <input
                            type="file"
                            id='imgUpload'
                            onChange={handleImageUpload}
                        />
                        <AddImgBtn>
                            <label htmlFor="imgUpload">
                                <img src={imgBtn} alt="  " />
                            </label>
                        </AddImgBtn>
                    </InputImg>
                </AddImg>
              </SubContainer>
        </Container>
        </>
    );
}
