import React, { useState } from 'react'
import styled, { keyframes } from 'styled-components'
import { GlobalStyle } from '../../../Styles/reset.style'
import PopupModal from './PopupModal';

//  
const slideUp = keyframes`
  from {
    transform: translateY(100%); //  :  
  }
  to {
    transform: translateY(0);    //  :  
  }
`;

const BottomModalContainer = styled.div`
  position: fixed;
  bottom: 0;
  width: 100%;
  max-width: 390px;
  padding: 1em 2em;
  background-color: white;
  border: 1px solid #e9e9e9;
  border-radius: 10px 10px 0 0;
  z-index: 20;

  animation: ${slideUp} 0.2s ease-out forwards;

  @media (min-width: 768px) {
    max-width: none;
    width: 766px; 
    right: 0;
    left: 0; 
    margin: auto; 
  }
`

//  
const Report = styled.button`
  color: red;
  font-size: 14px;
  text-align: left;
  position: relative;
  padding: 20px 0;
  display: inline-block;
  width: 100%;
  height: 50px;

  &::before {
    content: ${props => props.$first ? '""' : 'none'};
    position: absolute;
    top: 0;
    left: 50%;
    transform: translateX(-50%);
    height: 3px;
    width: 100px;
    background-color: #cccccc;
  }
`

export default function BottomModal({ reports, setIsModalOpen }) {
  const [isPopupVisible, setIsPopupVisible] = useState(false);
  const [alertText, setAlertText] = useState("");
  const [selectedAction, setSelectedAction] = useState(null);

  // Report    
  const handleReportClick = (report) => {
    setSelectedAction(report); //    
    setAlertText(report.alertText); //   
    setIsPopupVisible(true); //   
  };

  //  ''    
  const handleConfirmAction = () => {
    if (selectedAction?.onSelect) {
      selectedAction.onSelect(); //    onSelect  
    }
    handlePopupClose(); //   
  };

  //         
  const handlePopupClose = () => {
    setIsPopupVisible(false); //   
    setIsModalOpen(false); //   
    setSelectedAction(null); //    
  };

  return (
    <>
      <GlobalStyle />
      <BottomModalContainer>
        {reports.map((report, index) => (
          <Report key={index} $first={index === 0} onClick={() => handleReportClick(report)}>
            {report.action}
          </Report>
        ))}
      </BottomModalContainer>

      {isPopupVisible && (
        <PopupModal
          isVisible={isPopupVisible}
          setIsVisible={handlePopupClose}
          alertText={alertText}
          cancelText=""
          confirmText=""
          onConfirm={handleConfirmAction}
          onCancel={handlePopupClose}
        />
      )}
    </>
  );
}

