import React, { useEffect, useState } from 'react'
import { Header, HeaderButton, SaveButton, InputTitle, InputImg, AddImgBtn, ProductInfo, AddTxtForm, Required, CategoryContainer, DropdownSelect, AddImgWrap } from './AddProduct.style'
import { GlobalStyle, Container, SubContainer } from '../../Styles/reset.style'
import { useNavigate } from 'react-router-dom'
import backBtn from '../../assets/image/icon-arrow-left.png'
import imgBtn from '../../assets/image/icon-img-button.png'
import axios from 'axios'

//   input 
function ProductInput({title, isRequired, placeholder, type = "text", value, onChange}) {
  return (
    <ProductInfo>
      <InputTitle>
        {title} 
        {isRequired && <Required>*</Required>}
      </InputTitle>
      
      {type === "textarea" ? (
        <textarea placeholder={placeholder} value={value} onChange={onChange}></textarea>
      ) : (
        <input type={type} placeholder={placeholder} value={value} onChange={onChange} />
      )}
    </ProductInfo>
  );
}

export default function AddProduct() {
  const navigate = useNavigate();

  //    
  const [category, setCategory] = useState("");
  const [imageUrl, setImageUrl] = useState("");
  const [productName, setProductName] = useState("");
  const [price, setPrice] = useState("");
  const [productLink, setProductLink] = useState("");
  const [description, setDescription] = useState("");

  const [isActive, setIsActive] = useState(false);

  
  //        
  useEffect(()=> {
    const isValid = imageUrl && isValidProductName(productName) && price && productLink && description;
    setIsActive(!!isValid)
  }, [imageUrl, productName, price, productLink, description])

  //       URL  
  const handleImgChange = async (e) => {
    const file = e.target.files[0];
    const formData = new FormData();
    formData.append("image", file);

    try {
      const res = await axios.post('https://api.mandarin.weniv.co.kr/image/uploadfile', formData, {
        headers: {
          "Content-type" : "multipart/form-data"
        }
      })

      let filename = res.data.filename; //   
      const fullPath = `https://api.mandarin.weniv.co.kr/${filename}`;
      setImageUrl(fullPath) //  URL  
    } catch(err) {
      console.error(err);
    }
  };

  //   
  const isValidProductName = (name) => { //  2~15  
    return name.length >=2 && name.length <= 15;
  }

  //       
  const handleSaveProduct = async () => {
    if(!isValidProductName(productName)) {
      alert(" 2~15  .");
      return;
    }
    if (isNaN(price) || price < 1) {
      alert(" 1    .");
      return;
    }
    if (!isActive) {
      alert("  .");
      return;
    }
    

    const token = localStorage.getItem("token"); //    

    //      
    const formattedProductName = `
      productName: ${productName}
      category: ${category} 
      description: ${description}`;

    const productData = {
      itemName: formattedProductName,
      price: Number(price),
      link: productLink,
      itemImage: imageUrl,
      category: category,
      description: description
    }

    try {
      //    
      const res = await axios.post('https://api.mandarin.weniv.co.kr/product', {
        product: productData, //   
      }, {
        headers: {
          Authorization : `Bearer ${token}`,
          "Content-type" : "application/json"
        },
      });

      console.log(res.data); //    
      navigate('/market'); //     
    } catch (error) {
      console.error(error); //     
      alert("  .")
    }
  };

  return (
    <>
      <GlobalStyle/>
      <Container>
        <Header>
          <HeaderButton onClick={()=>navigate(-1)}>
            <img src={backBtn} alt="" />
          </HeaderButton>
          <SaveButton $active={isActive} onClick={handleSaveProduct}></SaveButton>
          {/* '$'    => "  HTML       "  */}
        </Header>

        <SubContainer>
          <CategorySection category={category} setCategory={setCategory}/>
          <AddImgWrap>
            <InputTitle>  <Required>*</Required></InputTitle>
            <InputImg img={imageUrl}>
              <input 
                type="file" 
                id='imgUpload'
                onChange={handleImgChange}
              />
              <AddImgBtn>
                <label htmlFor="imgUpload">
                  <img src={imgBtn} alt="  " />
                </label>
              </AddImgBtn>
            </InputImg>
          </AddImgWrap>

          <ProductDetails 
            productName={productName} setProductName={setProductName}
            price={price} setPrice={setPrice}
            productLink={productLink} setProductLink={setProductLink}
            description={description} setDescription={setDescription}
          />
        </SubContainer>
      </Container>
    </>
  )
}

function CategorySection({category, setCategory}) {
  return (
    <CategoryContainer>
      <InputTitle> <Required>*</Required></InputTitle>
      <DropdownSelect value={category} onChange={e => setCategory(e.target.value)}>
        <option value=""></option>
        <option value="bg_dogs"></option>
        <option value="bg_cats"></option>
        <option value="bg_etc."></option>
      </DropdownSelect>
    </CategoryContainer>
  )
}

function ProductDetails({productName, setProductName, price, setPrice, productLink, setProductLink, description, setDescription}) {
  return(
    <AddTxtForm>
      <ProductInput 
        title="" 
        isRequired={true}
        placeholder="2~15  ."
        value={productName}
        onChange={e=>setProductName(e.target.value)}
      />
      <ProductInput 
        title="" 
        isRequired={true}
        placeholder="  ." 
        type="number"
        value={price}
        onChange={e=>setPrice(e.target.value)}
      />
      <ProductInput 
        title=" " 
        isRequired={true}
        placeholder="URL  ."
        value={productLink}
        onChange={e=>setProductLink(e.target.value)}
      />
      <ProductInput 
        title=" " 
        isRequired={true}
        placeholder="   ."
        value={description}
        onChange={e=>setDescription(e.target.value)}
        type='textarea'
      />
    </AddTxtForm>
  )
}